body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f7f7f7;
}

@font-face {
  font-family: 'SF-Pro-Display-Semibold';
  src: local('SF-Pro-Display-Semibold'), url(./fonts/SF-Pro-Display-Semibold.otf) format('woff');
}

@font-face {
  font-family: 'SF-Pro-Display-Medium';
  src: local('SF-Pro-Display-Medium'), url(./fonts/SF-Pro-Display-Medium.otf) format('woff');
}

@font-face {
  font-family: 'SF-Pro-Text-Regular';
  src: local('SF-Pro-Text-Regular'), url(./fonts/SF-Pro-Text-Regular.otf) format('woff');
}

*::-webkit-scrollbar {
  display: none;
}